    font-family: 'Berkshire Swash', cursive;
    /* font-family: ; */
    
}
.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 29px;
    top:4px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc !important;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 4px;
    bottom: 4px;
    background-color: white !important;
    -webkit-transition: .4s;
    transition: .4s;
    background-image: url("https://upload.wikimedia.org/wikipedia/commons/3/37/Darkmode_moon_shining_stars.svg") !important;
    /* background: 'https://upload.wikimedia.org/wikipedia/commons/3/37/Darkmode_moon_shining_stars.svg' !important; */

  }
  
  input:checked + .slider {
    background-color: #2196F3  !important;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3 !important;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }